@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
  padding-left: 10px;
  padding-right: 10px;
}

body {
  margin: 0;
  background-color: #3f3f3f;
}

html a{
  text-decoration: none;
}

html .reactButton{

  padding: 5px 0px;
  font-size: 20px;
  background-color:white;
  color: black;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 8%;
  text-align: center;
  min-width: 105px;
  text-decoration: none;
  border-radius: 3px;
  transition: 0.25s all ease-in-out;
  margin-top: 25px;
  margin-bottom: 25px;
}

html .reactButton:hover{
  background-color: #6495ED;
  color: white;
}

html .show-time{
  display: none;
  text-align: center;
  font-size: 20px;
}

html .show-text{
  text-align: center;
  font-size: 20px;
}

html .show-count{
  text-align: center;
  font-size: 20px;
  color: white;
}

html p.position-copy-text{
  text-align: center;
  color: white;
}

html h2{
  color: white;
  font-size: 20px;
}

html h2.page-title{
  max-width: 50rem;
  margin: auto;
  text-align: left;
}

html span.welcome-text{
  text-align: center;
  display: block;
  margin-top: 1rem;
}

/* Desktop */

@media only screen and (max-width: 768px){
  html h2{
    font-size: 16px;
  }
}
.expense-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.5rem;
    margin: 1rem 0;
    background-color: #4b4b4b;
  }
  
  .expense-item__description {
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;
    justify-content: flex-start;
    flex: 1 1;
  }
  
  .expense-item h2 {
    color: #3a3a3a;
    font-size: 1rem;
    flex: 1 1;
    margin: 0 1rem;
    color: white;
    text-align:center;
    padding-left: 1.5rem;
  }
  
  .expense-item__price {
    font-size: 1rem;
    font-weight: bold;
    color: white;
    background-image: linear-gradient(to right, #40005dad, #40005d);
    border: 1px solid white;
    padding: 0.5rem;
    border-radius: 12px;
    text-align: center;
  }

  @media only screen and (min-width: 800px){ .expense-item__price { width: 21%; } }
  @media only screen and (max-width: 799px){ 
    .expense-item__price { 
      width: 40%; 
      min-width: 100px; 
    } 

    html .expense-item .expense-item__description h2 {
      color: #3a3a3a;
      font-size: 1rem;
      flex: 1 1;
      margin: 0px;
      margin-right: 5px;
      color: white;
      text-align:center;
      padding-left: 0rem;
    }

}
  
  @media (min-width: 580px) {
    .expense-item__description {
      flex-direction: row;
      align-items: center;
      justify-content: flex-start;
      flex: 2 1;
    }
  
    .expense-item__description h2 {
      font-size: 1.25rem;
    }
  
    .expense-item__price {
      font-size: 1.25rem;
      padding: 0.5rem 1.5rem;
    }
  }

  h2{
    text-align: center;
  }
.expense-date {
    display: flex;
    flex-direction: column;
    width: 5.5rem;
    height: 5.5rem;
    border: 1px solid #ececec;
    background-color: #2a2a2a;
    color: white;
    border-radius: 12px;
    align-items: center;
    justify-content: center;
  }
  
  .expense-date__month {
    font-size: 0.75rem;
    font-weight: bold;
  }
  
  .expense-date__year {
    font-size: 0.75rem;
  }
  
  .expense-date__day {
    font-size: 1.5rem;
    font-weight: bold;
  }
.card{

    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    border-radius: 12px;

}
.expenses-filter {  color: white; }
  
.expenses-filter__control {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  margin: 1rem 0;
}
  
.expenses-filter label {
  font-weight: bold;
  margin-bottom: 0.5rem;
}
  
.expenses-filter select {
  font: inherit;
  padding: 0.5rem 3rem;
  font-weight: bold;
  border-radius: 6px;
}
.expenses {
    padding: 1rem;
    background-color: rgb(31, 31, 31);
    margin: 2rem auto;
    width: 50rem;
    max-width: 95%;
  }
.expenses-list {
    list-style: none;
    padding: 0;
  }
  
  .expenses-list__fallback {
    color: white;
    text-align: center;
  }
.chart-bar {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .chart-bar__inner {
    height: 100%;
    width: 100%;
    border: 1px solid #313131;
    border-radius: 12px;
    background-color: #c3b4f3;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }
  
  .chart-bar__fill {
    background-color: #4826b9;
    width: 100%;
    transition: all 0.3s ease-out;
  }
  
  .chart-bar__label {
    font-weight: bold;
    font-size: 0.5rem;
    text-align: center;
  }
.chart {
    padding: 1rem;
    border-radius: 12px;
    background-color: #f8dfff;
    text-align: center;
    display: flex;
    justify-content: space-around;
    height: 10rem;
  }
.new-expense {
    background-color: #a892ee;
    padding: 1rem;
    margin: 2rem auto;
    width: 50rem;
    max-width: 95%;
    border-radius: 12px;
    text-align: center;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
  }
  
  .new-expense button {
    font: inherit;
    cursor: pointer;
    padding: 0.5rem 2rem;
    border: 1px solid black;
    background-color: white;
    color: black;
    border-radius: 6px;
    margin-top: 1rem;
    margin-bottom: 1rem;
    transition: all 0.2s ease-in-out;
    margin-left: 4.9%;
    width: 34%;
  }

  @media only screen and (min-width: 768px) and (max-width: 800px) {

    html .new-expense button.cancel-button {
      margin-left: 1rem;
      padding: 0.5rem 1rem;
      width: 40%;
    }

    html .new-expense .new-expense__control button[type="submit"]{
      margin-left: 4.9%;
      width: 50%;
      padding: 0.5rem 0.7rem;
    }
  }

  @media only screen and (max-width: 767px){

    
    html .new-expense button{
      width: 50%;
    }

    html .new-expense button{ 
      margin-left: 0rem;
    }

    html .new-expense button.cancel-button {
      margin-left: 0rem;
      padding: 0.5rem 1rem;
      width: 40%;
    }

    html .new-expense .new-expense__control button[type="submit"]{
      margin-left: 5%;
      width: 55%;
      padding: 0.5rem 0.7rem;
    }

  }
  
  .new-expense button:hover,
  .new-expense button:active {
    color: white;
    background-image: linear-gradient(to right, #40005dad, #40005d);
    background-color: transparent;
    border: 1px solid white;
  }

  html .new-expense button[type="submit"]{
    margin-left: 9.9%;
    width: 50%;
  }
  
  .new-expense button.alternative {
    color: #220131;
    border-color: transparent;
    background-color: transparent;
  }
  
  .new-expense button.alternative:hover,
  .new-expense button.alternative:active {
    background-color: #ddb3f8;
  }
  html .new-expense__controls form{
    width: 100%;
  }

  html .width-100{
    width: 100%;
  }

  html .margin-left-10{
    margin-left: 10%;
  }

  html div.new-expense__control{
    display:inline-block;
    padding-top: 1rem;
    width: 50%;
  }

@media only screen and (min-width: 768px){

  html .new-expense__control__padding{
    padding-left: 10%;
  }

  .new-expense__control label {
    font-weight: bold;
    margin-bottom: 0.5rem;
    display: block;
    font-size: 20px;
    margin-left: 1rem;
  }
  
  .new-expense__control input {
    font: inherit;
    padding: 0.5rem;
    border-radius: 6px;
    border: 1px solid black;
    max-width: 100%;
    margin-left: 1rem;
    width: 95%;
  }

  .new-expense__controls {
    display: flex;
    flex-wrap: wrap;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 0rem;
    text-align: left;
    justify-content: center;
    padding-left: 1rem;
    padding-right: 1rem;
  }

}

@media only screen and (max-width: 767px){

  html div.new-expense__control {
    display: inline-block;
    padding-top: 1rem;
    width: 100%;
  }

  .new-expense__control label {
    font-weight: bold;
    margin-bottom: 0rem;
    display: block;
    font-size: 20px;
    margin-left: 0rem;
  }
  
  .new-expense__control input {
    font: inherit;
    padding: 0.5rem;
    border-radius: 6px;
    border: 1px solid black;
    width: 100%;
    max-width: 100%;
    margin-left: 0rem;
  }

  .new-expense__controls {
    display: flex;
    flex-wrap: wrap;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 0rem;
    text-align: left;
    justify-content: center;
    padding-left: 1rem;
    padding-right: 1rem;
  }

}

  .new-expense__actions {
    text-align: right;
  }

